@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{font-family:'Roboto', sans-serif;padding:0;margin:0;box-sizing:border-box}body{background:#13131c;min-height:100vh;height:100%;width:100%}

.authInput{background:#2B2B2E;border:1px solid #48484C;border-radius:58px;width:250px;height:50px;padding:10px 20px;font-size:16px;color:white;outline:none}

.auth{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.auth .authInput{margin:10px}.auth button{width:220px;height:52px;left:487px;top:711px;background:#2B2B2E;border-radius:46px;border:none;outline:none;color:white;margin-top:20px}.auth button.signup{background:#FF003D}.auth button.signin{background:#6C63FF}.auth button.true{background:#5f5f5f}

.preloader{display:flex;justify-content:center;align-items:center;width:100%;min-height:100vh;height:100%;background:black}.preloader .dot{position:relative;width:10px;height:10px;margin:5px;border-radius:100%;background:white}.preloader .dot.first,.preloader .dot.last{-webkit-animation:preloader infinite linear;animation:preloader infinite linear;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-delay:0.5s;animation-delay:0.5s}.preloader .dot.second{-webkit-animation:preloader infinite linear;animation:preloader infinite linear;-webkit-animation-duration:1s;animation-duration:1s}@-webkit-keyframes preloader{0%{bottom:0px}50%{bottom:10px}100%{bottom:0px}}@keyframes preloader{0%{bottom:0px}50%{bottom:10px}100%{bottom:0px}}

.matchMaking{width:100%;min-height:100vh;height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}

.userData{width:250px;height:120px;background:#1d1d2a;box-shadow:0 12px 26px rgba(0,0,0,0.4);border-radius:10px;margin-bottom:20px;color:white;position:relative}.userData .textData{position:absolute;left:60px;top:7px}.userData .avatar{width:50px;height:50px;background:gray;border-radius:10px 0 0;position:absolute;left:0;top:0}.userData .bio{position:absolute;bottom:10px;left:10px}

.party{width:250px;height:400px;background-color:#1d1d2a;border-radius:10px;box-shadow:0 12px 26px rgba(0,0,0,0.4);color:white}.party .member{width:90%;margin:10px;height:40px;font-size:16px;background:#2b2b2e;border-radius:10px;display:flex;align-items:center;justify-content:center}.party .member.addMember{background:#48484e;cursor:pointer}.party .member.cancel{background:#f52944;cursor:pointer}.party .overlay{position:fixed;width:100%;height:100%;z-index:30;top:0;left:0;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);display:flex;align-items:center;justify-content:center}.party .overlay.false{display:none}.party .overlay .invite{display:flex;align-items:center;justify-content:center;flex-direction:column;background:#1d1d2a;border-radius:20px;padding:20px;box-shadow:3px 4px 9px rgba(0,0,0,0.25)}.party .overlay .invite input{width:250px;height:40px;background:#2b2b2e;border:none;border-radius:20px;outline:none;color:white;font-size:16px;padding:0 20px}

.btns{width:112px;height:40px;font-size:16px;color:white;border:none;border-radius:20px;margin:10px;outline:none;cursor:pointer}.btns.cancel{background:#e74c3c}.btns.submit{background:#2bb15e}

.overlay{position:fixed;width:100%;height:100%;z-index:30;top:0;left:0;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);display:flex;align-items:center;justify-content:center}.overlay.false{display:none}

.search{margin-bottom:20px}.search button{color:white;font-size:16px;border:none;border-radius:30px;width:250px;height:40px;background:#2bb15e;cursor:pointer;transition:0.5s;outline:none}.search button.red{background:#e74c3c}.search button.shake{-webkit-animation:shake 0.5s;animation:shake 0.5s}@-webkit-keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}@keyframes shake{0%{transform:translate(1px, 1px) rotate(0deg)}10%{transform:translate(-1px, -2px) rotate(-1deg)}20%{transform:translate(-3px, 0px) rotate(1deg)}30%{transform:translate(3px, 2px) rotate(0deg)}40%{transform:translate(1px, -1px) rotate(1deg)}50%{transform:translate(-1px, 2px) rotate(-1deg)}60%{transform:translate(-3px, 1px) rotate(0deg)}70%{transform:translate(3px, 1px) rotate(-1deg)}80%{transform:translate(-1px, -1px) rotate(1deg)}90%{transform:translate(1px, 2px) rotate(0deg)}100%{transform:translate(1px, -2px) rotate(-1deg)}}

.navbar{position:fixed;bottom:0;height:35px;width:100%;background:#1d1d2a;display:grid;align-items:center;justify-content:center;grid-template-columns:45px 45px 45px;grid-column-gap:40px;-webkit-column-gap:40px;column-gap:40px;z-index:20}.navbar .navLink{color:white;text-decoration:none;display:flex;align-items:center;justify-content:center;text-align:center;transition:0.3s;font-size:18px}.navbar .navLink.active{color:#2dff7d}.navbar .navLink:hover{color:#2dff7d}

.addFriend{display:flex;align-items:center;justify-content:center;flex-direction:column;background:#1d1d2a;border-radius:20px;padding:20px;box-shadow:3px 4px 9px rgba(0,0,0,0.25)}.addFriend input{width:250px;height:40px;background:#2b2b2e;border:none;border-radius:20px;outline:none;color:white;font-size:16px;padding:0 20px}.friends{display:flex;align-items:center;justify-content:center;flex-direction:column}

.friend{width:250px;border-radius:10px;box-shadow:0 12px 26px rgba(0,0,0,0.4);color:white;margin:20px;padding:20px}.friend .data .username{font-size:20px;text-transform:capitalize}

.notifications .invite{background:#1d1d2a;border-radius:10px;padding:10px;margin-bottom:10px;width:300px;color:white;box-shadow:0 12px 26px rgba(0,0,0,0.4);transform-origin:right}

.notifications{position:fixed;top:10px;right:5px;z-index:30}.notification{transform-origin:right;-webkit-animation:notifAdd 0.35s ease-in-out;animation:notifAdd 0.35s ease-in-out}.notification.remove{-webkit-animation:notifRemove 0.35s ease-in-out;animation:notifRemove 0.35s ease-in-out}@-webkit-keyframes notifAdd{0%{transform:scaleX(0);transform:scale(0)}100%{transform:scaleX(1);transform:scale(1)}}@keyframes notifAdd{0%{transform:scaleX(0);transform:scale(0)}100%{transform:scaleX(1);transform:scale(1)}}@-webkit-keyframes notifRemove{0%{transform:scaleX(1) scale(1) scaleZ(1) scaleY(1)}100%{transform:scaleX(0) scale(0) scaleZ(0) scaleY(0)}}@keyframes notifRemove{0%{transform:scaleX(1) scale(1) scaleZ(1) scaleY(1)}100%{transform:scaleX(0) scale(0) scaleZ(0) scaleY(0)}}

.notificationsView{width:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.notificationsView .notification{background:#1d1d2a;padding:10px;border-radius:10px;width:300px;color:white;margin:20px}

