.btns {
    width: 112px;
    height: 40px;

    font-size: 16px;
    color: white;

    border: none;
    border-radius: 20px;

    margin: 10px;

    outline: none;

    cursor: pointer;

    &.cancel {
        background: #e74c3c;
    }
    
    &.submit {
        background: #2bb15e;
    }
}

